import _ from 'lodash'

/**
  * @function toType 
  * @param obj 
*/
export function toType (obj) {
  return ({}).toString.call(obj).match(/\s([a-zA-Z]+)/)[1].toLowerCase()
}

/**
  * @function filter null
  * @param obj 
*/
export function filter (obj) {
  for (var key in obj) {
    if (obj[key] == null) {
      delete obj[key]
    }
    if (toType(key) === 'string') {
      obj[key] = _.trim(obj[key])
    } else if (toType(obj) === 'object' || toType(obj) === 'array') {
      obj[key] = filter(obj[key])
    }
  }
  return obj
}

/**
  * @function hasClass 
  * @param obj dom
  * @param cls 
*/
function hasClass (obj, cls) {
  return obj.className.match(new RegExp('(\\s|^)' + cls + '(\\s|$)'))
}

/**
  * @function addClass 
  * @param obj dom
  * @param cls 
*/
function addClass (obj, cls) {
  if (!hasClass(obj, cls)) {
    if (obj.className == '') obj.className += cls
    else obj.className += ' ' + cls
  }
}

/**
  * @function removeClass 
  * @param obj dom
  * @param cls 
*/
function removeClass (obj, cls) {
  if (hasClass(obj, cls)) {
    let reg = new RegExp('(\\s|^)' + cls + '(\\s|$)')
    let regMark = ' ';
    if (obj.className.split(' ').length === 1) regMark = ''
    obj.className = obj.className.replace(reg, regMark)
  }
}

/**
  * @function toggleClass 
  * @param obj dom
  * @param cls 
*/
export function toggleClass (obj, cls) {
  if (hasClass(obj, cls)) {
    removeClass(obj, cls)
  } else {
    addClass(obj, cls)
  }
}

/**
  * @function random 
  * @param lower 
  * @param upper 
  * @return 
*/
export function random(lower, upper) {
	return Math.floor(Math.random() * (upper - lower)) + lower
}

/**
  * @function fmtDate 
  * @param obj 
*/
export function fmtDate(obj){
    var date =  new Date(obj)
    var y = 1900+date.getYear()
    var m = "0"+(date.getMonth()+1)
    var d = "0"+date.getDate()
    return y+"."+m.substring(m.length-2,m.length)+"."+d.substring(d.length-2,d.length)
}



