<!--  -->
<template>
  <div class="ar-stick" @click="scrollUp" v-if="show">
    <img :src="icon" class="stick-icon" />
  </div>
</template>

<script>
export default {
  data () {
    return {
      show: false,
      icon: '/static/images/common/up.png'
    }
  },
  mounted () {
    this.initLoad()
  },
  methods: {
    initLoad () {
      window.requestAnimFrame = (() => {
        return window.requestAnimationFrame ||
               window.webkitRequestAnimationFrame ||
               window.mozRequestAnimationFrame ||
               function (callback) {
                 window.setTimeout(callback, 1000 / 60)
               }
      })()
      this.$nextTick(() => {
        window.addEventListener('scroll', this.needToTop) // 
      })
    },
    scrollUp () {
      let timer = null
      timer = () => {
        let curHeight = document.documentElement.scrollTop || document.body.scrollTop // 
        let now = curHeight
        let speed = (0 - now) / 7 // 
        speed = speed > 0 ? Math.ceil(speed) : Math.floor(speed)
        if (curHeight > 0) { // ,
          window.requestAnimationFrame(timer)
        }
        document.documentElement.scrollTop = curHeight + speed// ,needtotop
        document.body.scrollTop = curHeight + speed// 
      }
      window.requestAnimationFrame(timer)
    },
    needToTop () {
      let curHeight = document.documentElement.scrollTop || document.body.scrollTop
      this.show = curHeight > 0
    }
  }
}
</script>



